.sidebarRow{
    display: flex;
    padding: 10px;
    align-items: center;
    cursor: pointer;
    margin-left: 5px;
}
.sidebarRow:hover{
    background-color: lightgray;
    border-radius: 10px;
}
.sidebarRow > h4{
    margin-left: 10px;
    font-weight: 600;
}
.sidebarRow > .MuiSvgIcon-root{
    font-size: xx-large;
    color: #1877f2;
}